.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Actions {
    margin: 50px auto;
    width: 320px;
    padding: 15px;
    position: relative;
    text-align: center;
  }

.ActionItem {
    margin: 7px;
    border-radius: 5px;
    border: 2px solid #73c585;
    -webkit-box-shadow: 0 4px 6px #ccc;
            box-shadow: 0 4px 6px #ccc;
    padding: 10px;
    width:75px;
    height: 20px;
    text-align: center;
    font-size: 17px;
    font-weight: bold;
    vertical-align: middle;
    display: inline-block;
  }

  .ActionItemSelected {
      background: #cecdcd;
      margin: 7px;
      border-radius: 5px;
      border: 2px solid #cecdcd;
      -webkit-box-shadow: 0 4px 6px #ccc;
              box-shadow: 0 4px 6px #ccc;
      padding: 10px;
      width:75px;
      height: 20px;
      text-align: center;
      font-size: 17px;
      font-weight: bold;
      vertical-align: middle;
      display: inline-block;
    }

  div span{
    color: #DF612B;
  }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



